import React from 'react'
import Link from 'gatsby-link'
import styles from "./index.module.css";
import Container from "../layouts/container";
import Header from "../components/header";
import me from '../me.png';

const Intro = props =>
  <div className={styles.user}>
    <img src={props.avatar} className={styles.avatar} alt="" />
    <div className={styles.description}>
      <h2 className={styles.hello}>
        {props.hello}
      </h2>
      <p className={styles.excerpt}>
        {props.excerpt} <a href="/contact"> work! </a>
      </p>
    </div>
  </div>

const links = [`Work`, `CV`, `Contact`];
const workLink = <a href ="/contact"> work </a>;

export default () =>
  <Container>
    <h1>Rawaid Akhtar</h1>
    <Header links={links}/>
    <Intro
      hello="Hey, I'm Rawaid."
      avatar={me}
      excerpt={`I'm a web developer, musician, and aspiring comic artist based out of Brooklyn, NY. I'm currently available for  `}
    />

  </Container>


